:root {
	--main-color: #0072d3;
	--bg-pr: #fff;
	--bg-sc: #fafaff;
	--bg-th: #f2f2fc;
	--bg-black-50: #e8dfec;
	--text-color: #333;
	--p-color: #666;
	--boxshadow-color: #ddd;
	--main-transition: .3s;
	--main-border-radius:10px;
}
body.night {
	--bg-pr: #0f0f0f;
	--bg-sc: #222;
	--bg-th: #141416;
	--text-color: #eee;
	--p-color: #fafafa;
	--boxshadow-color: #333;
	--main-border-radius:10px;
}
