body.arabic {
	direction: rtl;
	font-family: 'Vazirmatn', sans-serif;
}
* {
	margin: 0;
	padding: 0;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}
html {
    scroll-behavior: smooth;
	font-family: 'Josefin Sans', sans-serif;
}
body {
	font-family: 'Josefin Sans', sans-serif;
	background-color: var(--bg-sc);
}
a {
	text-decoration: none;
}
li {
	list-style: none;
}
::selection {
	background-color: var(--main-color);
	color: #fff;
}
.container {
    padding-left: 15px;
    padding-right: 15px;
    margin-left: auto;
    margin-right: auto;
}
/* Small */
@media (min-width: 768px) {
    .container {
        width: 750px;
    }
}
/* Meduim */
@media (min-width: 992px) {
    .container {
        width: 970px;
    }
}
/* Large */
@media (min-width: 1200px) {
    .container {
        width: 1073px;
    }
}
/* GLOBAL CSS */
ul {
	margin: 0;
	padding: 0;
}
.links {
	margin-top: 20px;
}
.links a {
	display: inline-block;
    padding: 10px;
    background-color: var(--main-color);
    color: #fff;
    border-radius: var(--main-border-radius);
    margin-right: 20px;
	font-size: 18px;
    font-weight: 600;
}
section {
	padding-top: 50px;
	padding-bottom: 50px;
}
.title-parent {
	display: flex;
	justify-content: center;
}
.title-parent .main-title {
	text-align: center;
    margin-bottom:55px;
    font-size: 35px;
    color: var(--text-color);
    position: relative;
	width: fit-content;
	text-transform: uppercase;
}
.title-parent .main-title::before,
.title-parent .main-title::after {
	content: '';
	position: absolute;
	height: 5px;
	background-color: var(--main-color);
	left: 0;
}
.title-parent .main-title::before {
	width: 100%;
	bottom: -7px;
}
.title-parent .main-title::after {
	width: 50%;
	bottom: -20px;
}