.services {
	background-color: var(--bg-pr);
}
.services .container {
	display: flex;
	align-items: center;
    justify-content: center;
	flex-wrap: wrap;
	gap: 30px;
}
.services .container .service {
	width: 285px;
    text-align: center;
    padding: 50px 28px 30px;
    box-shadow: 0px 0px 8px 2px var(--boxshadow-color);
    transition: var(--main-transition);
    border-radius: var(--main-border-radius);
    position: relative;
}
.services .container .service:hover {
	transform: scale(1.06);
}
.services .container .service::before {
	content: '';
	position: absolute;
	width: 40%;
	height: 7px;
	background-color: var(--main-color);
	border-radius: 0 0 var(--main-border-radius) var(--main-border-radius);
	top: 0;
	left: 50%;
	transform: translateX(-50%);
	transition: var(--main-transition);
}
.services .container .service:hover::before {
	width: 50%;
}
.services .container .service img {
	width: 30%;
}
.services .container .service h3 {
	margin: 20px 0;
	text-transform: capitalize;
	font-size: 20px;
	color: var(--text-color);
    letter-spacing: 1px;
}
.services .container .service p {
	color: var(--p-color);
	line-height: 1.3;
	min-height: 104px;
}
.services .container .service .btn {
	margin: 20px auto 0;
    display: block;
    padding: 10px;
    border: 2px solid var(--main-color);
    border-radius: var(--main-border-radius);
    width: fit-content;
    color: var(--main-color);
	transition: var(--main-transition);
}
.services .container .service .btn:hover {
	color: #fff;
	background-color: var(--main-color);
}