/* Header */
body.arabic .toggle a {
	direction: ltr;
}
@media (max-width: 991px) {
    body.arabic header .container .menu {
		padding-top: 130px;
		right: unset;
		left: 0;
		transform: translateX(-330px);
	}
    body.arabic header .container .menu.open {
		transform: translateX(0);
	}
	body.arabic header .container .menu .close {
		right: 50px;
	}
	body.arabic header .container .menu li .toggle {
		right: 0;
		transform: translateX(11px);
	}
}