.about {
	background-color: var(--bg-pr);
	position: relative;
	z-index: 10;
}
.about .container {
	display: flex;
    justify-content: center;
    align-items: flex-start;
	gap: 70px;
}
.about .container .img {
	width: 275px;
    position: relative;
    transition: var(--main-transition);
}
.about .container .img::before {
	position: absolute;
    content: '';
    width: 275px;
    height: 385px;
    border: 7px solid var(--main-color);
    bottom: -28px;
    left: -50px;
    z-index: -1;
}
.about .container .img img{
	width: 100%;
	border-radius: var(--main-border-radius);
}
.about .container .def {
	flex-basis: 40%;
}
@media (max-width:991px) {
	.about .container {
		gap: 30px;
	}
	.about .container .def {
		flex-basis: 100%;
	}
	.about .container .img {
		display: none;
	}
}
.about .container h3 {
	font-size: 28px;
    color: var(--main-color);
    margin-bottom: 10px;
	text-transform: capitalize;
}
.about .container .def p {
	line-height: 2.6;
    color: var(--p-color);
    font-size: 17px;
    font-weight: 600;
	text-transform: capitalize;
}
@media (max-width:767px) {
	.about .container h3 {
		font-size: 25px;
	}
	.about .container .def p {
		font-size: 16px;
	}
}