.portfolio .container .categories {
	display: flex;
    justify-content: center;
    align-items: center;
    gap: 30px;
    margin-bottom: 30px;
}
.portfolio .container .categories .categorie {
	color: var(--text-color);
    padding: 10px;
	border: 2px solid var(--main-color);
    font-size: 18px;
    font-weight: 500;
    border-radius: var(--main-border-radius);
    cursor: pointer;
    transition: var(--main-transition);
}
.portfolio .container .categories .categorie.active,
.portfolio .container .categories .categorie:hover {
    background-color: var(--main-color);
    color: #fff;
}
.portfolio .container .projects {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    gap: 20px;
}
.portfolio .container .projects .project {
    flex-basis: 20%;
    position: relative;
    overflow: hidden;
}
@media (max-width:766px) {
    .portfolio .container .projects .project {
        flex-basis: 33%;
    }
}
.portfolio .container .projects .project.d-none {
    display: none;
}
.portfolio .container .projects .project a {
    position: absolute;
    background: linear-gradient(transparent, var(--main-color));
    width: 0;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    z-index: 100;
    transition: .1s;
}
.portfolio .container .projects .project:hover a {
    width: 100%;
}
.portfolio .container .projects .project .shadow{
    width: 100%;
    height: 100%;
    position: absolute;
    box-shadow: inset 0px 0px 30px -8px #242323;
    z-index: 99;
}
.portfolio .container .projects .project img{
    width: 100%;
    transition: var(--main-transition);
}
.portfolio .container .projects .project:hover img{
    transform: scale(1.2) rotate(10deg);
}
.portfolio .container .projects .project svg {
    position: absolute;
    right: -50%;
    transform: translateX(-50%);
    top: 60%;
    z-index: 1000;
    font-size: 45px;
    color: #fff;
    transition: var(--main-transition);
}
.portfolio .container .projects .project:hover svg {
    left: 50%;
}