.skills .container {
	display: flex;
	align-items: flex-start;
	justify-content: center;
	flex-wrap: wrap;
}
.skills .container .skill {
	flex-basis: 30%;
	display: flex;
	flex-direction: column;
	align-items: center;
	gap: 10px;
	margin-bottom: 20px;
}
.skills .container .skill h5 {
	text-transform: uppercase;
	font-size: 18px;
	color: var(--p-color);
}
@media (max-width:766px) {
	.skills .container {
		justify-content: space-between;
	}
	.skills .container .skill h5 {
		font-size: 16px;
	}
}
.skills .container .skill img {
	width: 100px;
}