.landing {
	padding-top: 100px;
	padding-bottom: 50px;
}
.landing .social-media {
	padding: 0 10px;
    /* background: var(--main-color); */
    width: fit-content;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50px;
    gap: 15px;
    margin: 20px 0;
}
.landing .social-media a {
	padding: 10px 5px;
    color: var(--text-color);
    font-size: 25px;
	transition: var(--main-transition);
}
.landing .social-media a:hover {
    color: var(--main-color);
	transform: scale(1.2);
}
.landing .container {
	display: flex;
    align-items: center;
    justify-content: center;
	height: 100%;
	/* flex-wrap: wrap; */
	position: relative;
	z-index: 100;
}
.landing .container .info {
	flex-basis: 60%;
}
.landing .container .info h1 {
	font-size: 55px;
    text-transform: capitalize;
	color: var(--text-color);
	font-weight: 500;
	line-height: 1.2;
}
.landing .container .info h1 span:nth-child(2) {
    text-transform: uppercase;
}
.landing .container .info h2 {
	font-size: 35px;
	color: var(--main-color);
	text-transform: capitalize;
	font-weight: 500;
	line-height: 1.2;
}
.landing .container .img {
	width: 375px;
	position: relative;
	padding: 25px;
	transition: var(--main-transition);
}
.landing .container .img::before {
	position: absolute;
    content: '';
    width: 100px;
    height: 100px;
    top: 0;
    right: 0;
    border-right: 6px solid var(--main-color);
    border-top: 6px solid var(--main-color);
	transition: var(--main-transition);
}
.landing .container .img::after {
	position: absolute;
    content: '';
    width: 100px;
    height: 100px;
    bottom: 0;
    left: 0;
    border-left: 6px solid var(--main-color);
    border-bottom: 6px solid var(--main-color);
	transition: var(--main-transition);
}
.landing .container .img:hover::before {
	width: calc(100% - 30px);
    height: calc(100% - 30px);
    top: 23px;
    right: 23px;
}
.landing .container .img:hover::after {
	width: calc(100% - 30px);
    height: calc(100% - 30px);
    bottom: 23px;
    left: 23px;
}
.landing .container .img img {
	width: 100%;
	border-radius: var(--main-border-radius);
}
@media (max-width:991px) {
	.landing {
		/* padding: 60px 0; */
		height: auto;
	}
	.landing .container {
		flex-wrap: wrap;
	}
	.landing .container .info {
		flex-basis: 100%;
		text-align: center;
	}
	.landing .container .info h1 {
		font-size: 50px;
	}
	.landing .container .info h1 span:nth-child(2) {
		font-size: 43px;
	}
	.landing .container .info h2 {
		font-size: 43px;
	}
	.landing .container .info .social-media {
		margin: 20px auto;
	}
	.landing .container .info .social-media a {
		font-size: 20px;
	}
	.landing .container .img {
		width: 300px !important;
		margin-top: 50px;
	}
}
@media (max-width: 767px) {
    .landing .container .info h1 {
		font-size: 37px;
	}
	.landing .container .info h1 span:nth-child(2) {
		font-size: 33px;
	}
	.landing .container .info h2 {
		font-size: 30px;
	}
	.landing .container .info .social-media {
		margin: 20px auto;
	}
	.landing .container .img {
		width: 250px !important;
	}
}